import { PiletApi } from "piral-core";
import * as React from "react";
import LandingPage from "../landing";

export function setupShell(app: PiletApi) {
  //   LAYOUTS REGISTRATION
  app.registerPageLayout("dashboard", ({ children }) => (
    <>
      <div>Header</div>
      <div>Navigation </div>
      {children}
      <div> footer </div>
    </>
  ));

  app.registerPageLayout("landing", ({ children }) => (
    <>
      <div>Header</div>
      <div>Navigation </div>
      {children}
      <div> footer </div>
    </>
  ));

  app.registerPageLayout("member", ({ children }) => (
    <>
      <div>Header</div>
      <div>Navigation </div>
      {children}
      <div> footer </div>
    </>
  ));

  app.registerPage("/land", LandingPage, {
    layout: "dashboard",
  });
}
